dialog {
  z-index: 1;
  width: auto;
  height: auto;
  font: inherit;
  color: inherit;
  background: none;
  border: 0;
  flex-flow: column;
  margin: 0;
  padding: 0;
  display: none;
  position: absolute;
  inset: 5px 0 -10px;
  overflow: visible;
}

@media (orientation: landscape) and (min-width: 920px) {
  dialog {
    width: 31.3vw;
    max-width: 800px;
    inset: 7vh auto 7vh -1px;
  }
}

dialog[open] {
  display: flex;
}

dialog:before {
  content: "";
  z-index: -1;
  filter: blur(2vh);
  background-color: #053db3;
  background-image: linear-gradient(140deg, #ff5f49 40%, #053db3 99%);
  display: block;
  position: absolute;
  inset: 4vh -2vh -2vh 4vh;
}

@media (orientation: landscape) {
  dialog:before {
    filter: blur(2.5vw);
    inset: 3.4vw -1.5vw -1.5vw 3.4vw;
  }
}

.dialog__body {
  background: #fff;
  flex-grow: 1;
  padding: 35px 20px 20px;
}

@media (orientation: landscape) and (min-width: 920px) {
  .dialog__body {
    padding-bottom: 30px;
  }
}

@media (orientation: landscape) and (min-width: 1920px) {
  .dialog__body {
    padding: 60px 45px 50px;
  }
}

@media (prefers-color-scheme: dark) {
  body:not(.t-light) .dialog__body {
    background: #030b19;
  }
}

body.t-dark .dialog__body {
  background: #030b19;
}

.dialog__btn-close {
  font-size: 30px;
  position: absolute;
  transform: translate(-50%, -50%)rotate(180deg);
}

